<style scoped lang="scss">
.farm-frame {
  border-radius: 16px;
  border: 2px solid $neutrals2;
  background-color: $neutrals1;
  padding: 24px;
  width: 600px;
  margin-bottom: 32px;
  font-size: 20px;
  transition: all 0.2s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  @include m {
    width: 500px;
  }
  @include a {
    width: 400px;
  }
}
button {
  padding: 14px 16px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
  width: 120px;
  font-size: 16px;
  margin-right: 8px;
  &:hover {
    background-color: darken($blue1, 10%);
  }
}
.primary {
  background-color: $blue1;
  &:hover {
    background-color: darken($blue1, 10%);
  }
}
.claim {
  background-color: $blue1;
  &:hover {
    background-color: darken($blue1, 10%);
  }
  width: 210px;
  margin-left: 4px;
  margin-right: 4px;
}
.approve {
  background-color: $blue1;
  &:hover {
    background-color: darken($blue1, 10%);
  }
  width: 300px;
}
.max {
  background-color: $green;
  &:hover {
    background-color: darken($green, 10%);
  }
