input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
input:focus {
  outline: none;
  border: 2px solid $blue3;
  box-shadow: 4px 4px 8px rgba($blue3, 0.1);
}

input {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 8px 12px;
  width: 300px;
  font-size: 20px;
  border-radius: 4px;
  /* Neutrals/6 */

  border: 2px solid $neutrals4;
  box-sizing: border-box;
}
.input-row {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 8px;
}

.farm-name {
  font-size: 32px;
  font-weight: 500;
  margin-bottom: 8px;
}
.arrow {
  width: 20px;
  height: 20px;
  fill: $neutrals6;
  padding: 8px;
  border-radius: 4px;
  color: $neutrals6;
  cursor: pointer;
  margin-left: 8px;
  &:hover {
    background-color: $neutrals3;
  }
}
.farm-apr {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 8px;
}

.dropdown {
  margin-top: 16px;
  overflow: hidden;
  transition: max-height 0.4s ease-in-out; // note that we're transitioning max-height, not height!
  height: auto;
  max-height: 120px; // still have to hard-code a value!
}
.collapsed {
  max-height: 0px;
}
.rotate {
  transform: rotate(-180deg);
}
.arrow-svg {
  transition: all 0.3s;
}
