  display: flex;
  position: relative;
  align-items: center;
  &:hover .max-button {
    display: inline;
  }
}
.max-button {
  position: absolute;
  right: 4px;
  display: none;
  color: $neutrals2;
  font-size: 20px;
  padding: 8px;
