  border: 2px solid $neutrals4;
  width: 200px;
  background-color: $neutrals1;

  &:hover {
    border: 2px solid $neutrals5;
    background-color: darken($neutrals1, 10%);
  }
}
.pool {
  width: 300px;
  padding: 14px 16px;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 16px;

  margin-top: 12px;
