  padding: 8px;
  margin: 8px;
}
</style>
<template>
  <div class="farm-frame">
    <div class="farm-name">{{ nameText }} Pool</div>
    <div class="farm-apr">
      <div>APR :{{ APR }} </div>
      <br/>
     
      <div class="arrow" @click="toggleDropdown">
        <img
          class="arrow-svg"
          :class="{ rotate: dropdownShown }"
          src="@/assets/down-arrow.svg"
          alt=""
        />
      </div>
    </div>
    <button class="connect" v-if="!isConnected" @click="connectWallet">
      Connect Wallet
    </button>

    <div class="actions" v-else>
      <a
        v-if="balanceOfStakedToken == 0 && depositedByUser == 0"
        class="connect pool"
        target="_blank"
        :href="getOwned"
      >
        {{ noOwnedText }}
      </a>

      <div v-else>
        <div v-if="!isApproved">
          <button @click="approveMax" class="approve">
            Approve {{ stakedTokenName }}
          </button>
        </div>
        <div v-else>
          <div class="input-row" v-if="balanceOfStakedToken > 0">
            <button @click="depositStake" class="primary">Stake</button>
            <div class="input">
              <input type="number" v-model="stakeAmount" />
              <div class="max-button" @click="setDepositMax">Max</div>
            </div>
          </div>
          <div class="input-row" v-if="depositedByUser > 0">
            <button @click="withdrawStake" class="primary">Withdraw</button>
            <div class="input">
              <input v-model="withdrawAmount" type="number" />
              <div class="max-button" @click="setWithdrawMax">Max</div>
            </div>
          </div>

          <div class="input-row" v-if="rewardsEarned > 0">
            <button class="claim" @click="getReward">
              Claim {{ formatUnits(rewardsEarned, 6) }} {{ rewardTokenName }}
            </button>
            <button class="claim" @click="exit">Claim and withdraw all</button>
          </div>
        </div>
      </div>
      <div class="dropdown" :class="{ collapsed: !dropdownShown }">
        <div class="dropdown-row">
          Your Stake : {{ formatUnits(depositedByUser, 6) }}
          {{ stakedTokenName }}
        </div>
        <div class="dropdown-row">
          Your Unclaimed Rewards : {{ formatUnits(rewardsEarned, 6) }}
          {{ rewardTokenName }}
        </div>
        <div class="dropdown-row">
          {{ stakedTokenName }} Balance :
          {{ formatUnits(balanceOfStakedToken, 6) }}
        </div>
        <div class="dropdown-row">
          Total Staked : {{ formatUnits(totalStaked, 3) }} {{ stakedTokenName }}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { ethers } from "ethers";
import CARBON_POOL_ABI from "@/abi/CarbonRewards.json";
import ERC20_ABI from "@/abi/ERC20.json";

export default {
  props: ["address"],
  data() {
    return {
      stakeAmount: "0",
      withdrawAmount: "0",
      dropdownShown: false,
    };
  },

  computed: {
    nameText() {
      if (this.stakedToken === "0x0000000000000000000000000000000000000000") {
        return "ETH";
      }
      if (this.stakedToken === "0xfa42DA1bd08341537a44a4ca9D236D1c00A98b40") {
        return "BOOBA";
      }
      if (this.stakedToken === "0x08da83452Ae158c3F348d4e0789b7A78989f34eE") {
        return "ETH-BOOBA LP";
      }
     
      return "";
    },
    noOwnedText() {
      if (this.stakedToken === "0x0000000000000000000000000000000000000000") {
        return "You need to get ETH";
      }
      if (this.stakedToken === "0x2b87E92dA50e3ABc4aFebCD25bc567074778A3D3") {
        return "Deposit BOOBA-ETH LP on SENPAI";
      }
     
      return "";
    },
    getOwned() {
      if (this.stakedToken === "0xfa42DA1bd08341537a44a4ca9D236D1c00A98b40") {
        return "https://senpaiswap.com/#/swap";
      }
      if (this.stakedToken === "0x08da83452Ae158c3F348d4e0789b7A78989f34eE") {
        return "https://senpaiswap.com/#/swap";
      }
      if (this.stakedToken === "0xA5447642cb156C4459D9eeB86A552ad55b51509e") {
        return "https://senpaiswap.com/#/add/0x9AB1F99BaEFD4F809B971D1F84A93096c346DD16/ETH";
      }

      return "";
    },
    isApproved() {
      return this.$store.state.farm.farmInfo[this.address].approvedAmount.gt(0);
    },
    isConnected() {
      return this.$store.getters.isConnected;
    },
    rewardPrice() {
      return this.$store.state.farm.CarbonPrice;
    },
    rewardTokenName() {
      return this.$store.state.farm.farmInfo[this.address].rewardName;
    },
    stakedTokenName() {
      return this.$store.state.farm.farmInfo[this.address].stakedName;
    },
    totalStaked() {
      return this.$store.state.farm.farmInfo[this.address].totalSupply;
    },
    valueOfTotalStaked() {
      return this.$store.state.farm.farmInfo[this.address].valueOfTotalStaked;
    },
    depositedByUser() {
      return this.$store.state.farm.farmInfo[this.address].depositedByUser;
    },
    rewardsEarned() {
      return this.$store.state.farm.farmInfo[this.address].rewardsEarned;
    },
    balanceOfStakedToken() {
      return this.$store.state.farm.farmInfo[this.address].stakeTokenBalance;
    },
    balanceOfRewardToken() {
      return this.$store.state.farm.farmInfo[this.address].rewardTokenBalance;
    },
    stakedToken() {
      return this.$store.state.farm.farmInfo[this.address].stakedToken;
    },

    APR() {
      if (this.stakedToken == "0xA5447642cb156C4459D9eeB86A552ad55b51509e") {
        return "OOOh you're a brave one aint ya?"
      } else {
        return "A lot of free tokens without risk"
      }
      //let APR = this.$store.state.farm.farmInfo[this.address].APR;
      //return APR.substring(0, APR.indexOf(".") + 3) + "%";
    },
  },
  methods: {
    connectWallet() {
      this.$store.commit("setWalletConnectModalShown", true);
    },
    async approveMax() {
      let signer = window.web3.getSigner();

      let stakedContract = new ethers.Contract(
        this.stakedToken,
        ERC20_ABI,
        signer
      );
      await stakedContract.approve(
        this.address,
        "0xffffffffffffffffffffffffffffffffffffffff"
      );
    },

    toggleDropdown() {
      this.dropdownShown = !this.dropdownShown;
    },
    setDepositMax() {
      if (this.stakedTokenName === "ETH") {
        this.stakeAmount = ethers.utils.formatEther(
          this.balanceOfStakedToken.sub("3000000000000000")
        );
      } else {
        this.stakeAmount = ethers.utils.formatEther(this.balanceOfStakedToken);
      }
    },
    setWithdrawMax() {
      this.withdrawAmount = ethers.utils.formatEther(this.depositedByUser);
    },

    formatUnits(value, decimals = 2) {
      if (value == 0) {
        return "0";
      }
      let formatted = parseFloat(
        ethers.utils.formatUnits(value, 18)
      ).toLocaleString(undefined, { minimumFractionDigits: decimals });
      return formatted;
    },
    async getReward() {
      let signer = window.web3.getSigner();

      let poolContract = new ethers.Contract(
        this.address,
        CARBON_POOL_ABI,
        signer
      );

      await poolContract.getReward();
    },
    async exit() {
      let signer = window.web3.getSigner();

      let poolContract = new ethers.Contract(
        this.address,
        CARBON_POOL_ABI,
        signer
      );

      await poolContract.exit();
    },
    async withdrawStake() {
      let signer = window.web3.getSigner();

      let poolContract = new ethers.Contract(
        this.address,
        CARBON_POOL_ABI,
        signer
      );

      let withdrawAmount = ethers.utils.parseEther(
        this.withdrawAmount.toString()
      );

      await poolContract.withdraw(withdrawAmount);
    },

    async depositStake() {
      let signer = window.web3.getSigner();

      let poolContract = new ethers.Contract(
        this.address,
        CARBON_POOL_ABI,
        signer
      );

      let depositAmount = ethers.utils.parseEther(this.stakeAmount.toString());
      let options = {};
      if (this.stakedTokenName === "ETH") {
        options["value"] = depositAmount;
      }
      await poolContract.stake(depositAmount, options);
    },
  },
};
</script>